@use "config/variables" as var;

@for $i from 1 to 4 {
  h#{$i} {
    margin: 0 0 calc(var.$separacion / 2);
    line-height: 1.5;
  }
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

p {
  font-size: 2rem;
  color: var.$secondary-color;
}

[class$="__title"] {
  color: var.$white;
  text-align: center;

  span {
    display: block;
    font-size: 1.9rem;
  }
}
